.root {
  position: relative;
  display: block;
  height: 100%;
}
.close {
  position: absolute;
  right: 10px;
  top: 10px;
  color:red;
  cursor: pointer;
  font-size: 20px;
}
.controls {
  position: absolute;
  color: #d3cea7;
  font-size: 36px;
  margin-top: -18px;
  top: 50%;
  cursor: pointer;
}
.prev {
  @extend .controls;
  left: 10px;
}
.next {
  @extend .controls;
  right: 10px;
}