.root {
  position: fixed;
  padding: 5px;
  border: 1px solid #61b3ff;
  max-width: 180px;
  min-width: 140px;
  color: #425060;
  border-radius: 2px;
  background-color: #c6e4ff;
  text-align: left;
  transform: translate(-50%, 0);
  margin-bottom: 10px;
  z-index: 1000;
  &:after {
    content: '';
    position: absolute;
    bottom: -12px;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-top: 12px solid #61b3ff;
    left: -12px;
    margin-left: 50%;
  }
  &.hide {
    transform: translate(-10000px, 0);
  }
  &.bottom {
    margin-bottom: -12px;
    &:after {
      top: -12px;
      bottom: inherit;
      border-bottom: 12px solid #61b3ff;
      border-top: 0;
    }
  }
}
