.figureSearchButton, .figureSearchCloseButton {
  fill: #00801E;
  cursor: pointer;
}
.figureSearchButtonText {
  fill: #FFF;
  font-family: Verdana, Tahoma, Arial, sans-serif;
  cursor: pointer;
  pointer-events: none;
}
.figureSearchClose {
  stroke: white;
  fill: transparent;
  stroke-linecap: round;
  stroke-width: 2;
  cursor: pointer;
  pointer-events: none;
}

.resizableTL, .resizableBR{
  cursor: nw-resize;
  fill: #00801E;
}
.resizableTR, .resizableBL{
  cursor: ne-resize;
  fill: #00801E;
}
:global(.figure-marker) {
  cursor: move;
}