.root {
  background: #666;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
}

.fullScreen {
  display: block;
  position: absolute;
  right: 10px;
  top: 10px;
  color: #999;
}

.mosaic {
  position: relative;
  overflow-y: scroll;
  background: #f9f9f9;
  padding-top: 4px;
  padding-left: 4px;

  > div {
    display: block;
    overflow: hidden;
  }
  section {
    position: relative;
    display: block;
    float: left;
    margin-bottom: 4px;
    padding: 0 2px 0 0;
    > div {
      display: block;
      background: #fff;
      height: 100%;
      width: 100%;
      outline: 1px solid #eaeaea;
      overflow: hidden;
    }
    > span {
      display: none;
    }
    &:hover {
      .fullScreen {
        display: block;
        &:hover {
          color: #000;
          cursor: pointer;
        }
      }
    }
  }
}
.thumbs {
  position: absolute;
  right: 0;
  width: 134px !important;
  top: 34px;
  bottom: 0;
  background: #666;
  overflow-y: scroll;
  padding: 0;
  > div {
    display: block;
    margin-bottom: -8px;
    &:first-child {
      margin-top: 0;
    }
  }
  section {
    position: relative;
    display: block;
    margin: 9px auto;
    cursor: pointer;
    background-color: #fff;
    svg {
      pointer-events: none;
      g {
        pointer-events: none;
      }
    }

    &:hover {
      > span {
        display: inline-block;
      }
    }
    &:global(.active) {
      box-shadow: 0 0 0 9px #ffc120;
      overflow: hidden;
    }
    > span {
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 4px;
      line-height: 17px;
      text-align: center;
      display: none;
      background-color: #fff;
      color: #666;
    }
  }
}
.isEmpty {
  top: 0;
}

.sidebar {
  background: #fff;
  padding: .5rem;
  margin: 0 -10px;
  border: 1px solid #ccc;
  border-width: 0 0 1px 1px;
  height: 34px;
  display: flex;
  justify-content: space-between;
  button {
    display: inline-block;
  }
}

.bns {
  top: 0;
}

.thumbNumber {
  position: static;
}

.magnifier {
  svg {
    g, rect, use {
      pointer-events: none;
    }
  }
}
