.root {
  position: relative;
  overflow: hidden;
  user-select: text;
}
.background {
  user-select: none;
}
.image {
  position: absolute;
  left: 0;
  top: 0;
  user-select: none;
  pointer-events: none;
}
.annotations {
  position: absolute;
  left: 0;
  top: 0;
  user-select: none;
}
.annotations > rect{
  cursor: pointer;
}
.annotations use{
  cursor: move;
}

.annotations path.boxes{
  cursor: pointer;
}
.scale {
  transform-origin: 0 0;
}

.selectionZoom {
  pointer-events: none;
  fill: #22c;
  fill-opacity: 0.15;
  stroke: #22c;
  stroke-width: 0.5;
}
.selectionSearch {
  fill: #00801E;
  fill-opacity: 0.15;
  stroke: #00801E;
  stroke-width: 0.5;
  cursor: move;
}
.disableMarkers {
  use, path, rect {
    pointer-events: none;
  }
}
.selectionMove {
  cursor: move;
}
.imgRoot {
  position: absolute;
  overflow: hidden;
}
.searchSelection {
  cursor: crosshair !important;
}
.deleteMarkers use {
  cursor: pointer;
}
.loadingErrorContainer {
  pointer-events: none;
  svg {
    pointer-events: none;
  }
}
.loadingError {
  text-align: center;
  display: block;
  font-style: italic;
}
.loadingErrorContainer {
  .imgRoot {
    display: table;
  }
  .loadingError {
    display: table-cell;
    vertical-align: middle;
  }
}

