.root {
  position: absolute;
  z-index: 1;
  font-family: Verdana;
  pointer-events: none;
  color: #000 !important;
  :global {
    .active {
      width: 100%;
      height: 100%;
    }
  }
  > div {
    pointer-events: all;
    overflow: visible;
  }
  table {
    border: 0 !important;
    margin-bottom: 0;
    font-size: 0.9em;
    td {
      padding: 0.25rem !important;
      vertical-align: top !important;
      line-height: 1.43 !important;
      border:0 !important;
      &:first-child {
        width: 15px;
        padding: 0.12rem 0.25rem !important;
        font-style: normal;
      }
    }
  }
  :global {
    .panel {
      width: 99%;
      height: 99%;
    }
    .panel-body-wrapper {
      height: 100%;
      overflow-y: auto;
      border-bottom: 36px solid transparent;
      cursor: text;
    }
    .panel-body {
      user-select: text;
    }
  }
  :global(.panel-heading) {
    h4 {
      cursor: move;
      margin: 0 !important;
      font-size: 13px !important;
    }
    span {
      margin-top: -14px;
      font-size: 15px;
      margin-right: -6px;
      cursor: pointer;
    }
  }
}
.legendRow {
  span {
    border-radius: 0.75rem;
    padding: 0.2rem 0.4rem;
  }
}
.legendBody {
  padding-bottom: 5px;
  :global(.panel-body) {
    padding: 10px;
    overflow-y: auto;
    height: 100%;
    font-size: 16px;
    span {
      padding: 0 2px !important;
      border-radius: 6px;
    }
  }
}
:global {
  .react-draggable {
    > div {
      > div:nth-child(7) {
        background: url('data:image/png;base64,') no-repeat 0 0;
        right: 0 !important;
        bottom: 0 !important;
      }
    }
  }
}

