.root {
  position: absolute;
  left: -10000px;
  top: -10000px;
  rect {
    fill-opacity: 0.65;
    cursor: move;
  }
  text {
    fill-opacity: 0.85;
    stroke: none;
    font-family: sans-serif;
  }
  :global {
    .deletion {
      cursor: pointer;
      pointer-events: none;
    }
    .deletion-group {
      cursor: pointer;
      rect {
        cursor: pointer;
      }
    }
  }
}
