.limit {
  position: absolute;
  top: 50%;
  left: calc(50% - 134px);
}
.limit > div {
  padding: 10px 10px 0 10px;
}
.buttons .info {
  background-color: #99d4ec;
  border: 1px solid #6ca3b8;
  color: #fff;
  padding: 8px;
  margin-right: 5px;
}
.buttons span {
  display: inline-block;
  padding: 0 5px;
}
.change {
  border: 0 !important;
}
.settings {
  padding: 15px 0 0;
  text-align: center;
}
.limitValue {
  display: inline-block;
  width: 66px;
  text-align: center;
  margin-right: 5px;
}
