.speedRange {
  > div {
    display: none;
    height: 255px;
    position: absolute;
    z-index: 4;
    padding: 0;
    background: #fff;
    border: 1px solid #ccc;
    border-top-color: #fff;
    :global {
      .slider-vertical {
        .slider-handle {
          background: #888;
          border: 1px solid #555;
          border-radius: 0;
          height: 10px;
          margin-top: -5px;
          &:hover {
            background: #bbb;
            cursor: pointer;
          }
        }
        .slider-track {
          width: 1px;
          margin-top: 0;
          background: #999;
          left: 50%;
          margin-left: -1px;
        }
        .slider-tick {
          border-radius: 0;
          height: 1px;
          background: #999;
          margin-top: 0;
          width: 60%;
          margin-left: 20%;
          margin-right: 20%;
        }
        .slider-selection.tick-slider-selection {
          background: transparent;
        }
      }
    }
  }
  > button {
    border-radius: 0;
  }
  &:hover {
    > div {
      display: block;
      text-align: center;
      width: 100%;
    }
  }
}

.speedRangeValue {
  min-width: 20px;
  display: inline-block;
  position: absolute;
  right: 0;
  font-size: 10px;
  top: 0;
  text-align: center;
}
.awesomeIcons {
  font-size: 18px;
  color: #555;
}
.labels {
  font-size: 10px;
  color: #999;
  display: block;
  height: 22px;
  line-height: 17px;
  &:last-child {
    line-height: 24px;
  }
}
