.root {
  height: 34px;
  border-bottom: 1px solid #ccc;
  padding: .5rem .2rem;
  position: absolute;
  top: 0;
  right: 0;
  background: #f9f9f9;
  button {
    padding: 0;
    display: block;
  }
  > div, > button {
    display: inline-block;
    &:first-child {
      margin-left: 0;
    }
    &:last-child {
      margin-right: 0;
    }
  }

  .navButton {
    min-height: 22px;
    max-height: 22px;
    line-height: 22px;
    width: 25px;
    font-size: 13px;
    font-family: Arial, sans-serif;
    color: #327ab7;
    background-color: #fff;
    border: 1px solid #d7d7d7;
    border-radius: 0;
    &:hover {
      color: #23527c;
     }
  }
}

.mosaic {
  border-left: 0;
  width: auto;
}

.item {
  font-weight: normal;
  margin: 0 2px;
  padding-right: .25rem;
  padding-left: .25rem;
  cursor: default;
  border-radius: 2px;
  line-height: 20px;
  height: 20px;
  border: 1px solid black;
  font-size: 1.2rem;
  &.inactiveItem {
    border: 1px solid transparent;
    background: transparent !important;
  }
}
.legendIcon {
  border: 0;
  height: 18px;
  line-height: 18px;
  display: inline;
}
.legend {
  text-align: right;
  position: absolute;
  top: -24px;
  right: 10px;
  z-index: 101;
}

