.root {
  z-index: 3;
  background: #f9f9f9;
  border-bottom: 1px solid #ccc;
  line-height: 28px;
  padding-right: 80px;
  button {
    border-radius: 0;
    border-bottom: 0;
    padding-left: 8px;
    padding-right: 8px;
    min-width: 34px;
    height: 33px;
  }
  :global {
    .btn {
      border-top: 1px solid transparent;
      background: #f9f9f9;
      outline: none !important;
      padding-bottom: 5px;
    }
    .btn-primary {
      background: #666;
      > span {
        color: #62AAC2;
      }
    }
  }
}

.mode {
  margin: 0 20px;
  display: inline-block;
  :global {
    .slider.slider-horizontal {
      width: 104px;
      .slider-handle {
        background: #888;
        border: 1px solid #555;
        border-radius: 0;
        width: 10px;
        margin-left: -5px;
        &:hover {
          background: #bbb;
          cursor: pointer;
        }
      }
      .slider-track {
        height: 1px;
        margin-top: 0;
        background: #555;
      }
      .slider-tick {
        border-radius: 0;
        width: 1px;
        background: #555;
        margin-left: 0;
      }
      .slider-selection.tick-slider-selection {
        background: transparent;
      }
    }
  }
}

.dropdown {
  margin-left: -1px;
  margin-top: 0;
  border-radius: 0;
}

.markers {
  a {
    &:focus {
      background: transparent !important;
    }

  }
}

.denominator {
  display: inline-block;
  position: absolute;
  right: 2px;
  font-size: 10px;
  top: 0;
  text-align: center;
}

.dropDownButtons {
  :global {
    .btn {
      border-left: 0;
    }
    ul.dropdown-menu {
      width: 40px;
      min-width: 40px;
      li {
        a {
          padding-left: 9px;
          padding-right: 9px;
          text-align: center;
        }
      }
    }
  }
}

.icon {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: 'ribbonicons';
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.iconFigureControls {
  margin-top: -2px;
  @extend .icon;
  &:before {
    content: '\1f304';
  }
}

.iconFigureInfo {
  @extend .icon;
  &:before {
    content: '\f129';
  }
}

.awesomeIcons {
  font-size: 18px;
  color: #555;
}

.searchSelection {
  display: block;
  height: 16px;
  width: 16px;
  float: none !important;
  margin: 2px auto;
}

.modeButtons {
  svg {
    display: block;
    height: 15px;
    width: 15px;
    float: none !important;
    margin: 3px auto 2px;
  }
}

.modeButtonsLeft {
  @extend .modeButtons;
  border-left: 0;
  border-right: 0;
  margin-right: -12px;
}

.modeButtonsRight {
  @extend .modeButtons;
  border-left: 0;
  margin-left: -12px;
}

.modesWrap {
  display: inline-block;
}
