.root {
  position: absolute;
  z-index: 25;
  background: #fff;
  background: transparent;
}
.glass {
  outline: 2px solid rgb(82, 168, 236);
  box-shadow: 1px 1px 3px rgba(82, 168, 236, 0.6);
  display: block;
  position: absolute;
  pointer-events: none;
}
.zoomed {
  background: #fff;
  display: block;
  overflow: hidden;
}
.zoomedContainer {
  outline: 2px solid rgb(82, 168, 236);
  box-shadow: 1px 1px 3px rgba(82, 168, 236, 0.6);
  position: absolute;
  top: 0;
  background: #fff;
  overflow: hidden;
}

.arrow {
  position: absolute;
  width: 0;
  height: 0;
  left: -10px;
  top: 48%;
  border: 10px solid rgba(82, 168, 236, 0.9);
  &.left{
    border-left: 0;
    border-top-color: transparent;
    border-bottom-color: transparent;
  }
  &.right{
    border-right: 0;
    border-top-color: transparent;
    border-bottom-color: transparent;
  }
  &.top{
    border-top: 0;
    border-left-color: transparent;
    border-right-color: transparent;
  }
  &.bottom{
    border-bottom: 0;
    border-left-color: transparent;
    border-right-color: transparent;
  }
}
.arrowLeft{
  @extend .arrow;
  border-left: 0;
  border-top-color: transparent;
  border-bottom-color: transparent;
}
.arrowRight{
  @extend .arrow;
  border-right: 0;
  border-top-color: transparent;
  border-bottom-color: transparent;
}
.arrowTop{
  @extend .arrow;
  border-top: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}
.arrowBottom{
  @extend .arrow;
  border-bottom: 0;
  border-left-color: transparent;
  border-right-color: transparent;
}
